@import '~assets/styles/h3Imports';

.root {
  display: grid;
  grid-template-columns: 30px em(170);
}

.address-wrapper {
  align-items: center;
}

.is-open-label {
  color: var(--accent-interactive, #284b9b);
  font-size: em(16);
  font-weight: 600;
  line-height: em(24);
}

.working-hours {
  color: var(--input-rested-border-color, #606565);
  font-size: em(14);
  font-weight: 600;
  line-height: em(20);
}
