@import '../../../../uikit/utils';
@import '~assets/styles/h3Imports';

.root {
  --accordion-background-color: var(--white, #ffffff);
  --accordion-item-box-shadow-color: var(
    --recommendation-card-box-shadow-color,
    rgba(10, 20, 20, 0.1)
  );

  background-color: var(--accordion-background-color);
  border-radius: em(8);
  box-shadow: 0 em(5) em(14) 0
    var(--accordion-box-shadow-color, var(--accordion-item-box-shadow-color));
  max-width: 1016px;

  @media #{$large-down} {
    max-width: none;
  }

  &:not(:first-child) {
    margin-top: em(24);
  }
}

.accordion-panel {
  &[data-accordion-panel-element='panel'] {
    padding-left: 0;
    padding-right: 0;

    @media #{$small-down} {
      padding-bottom: 0;
    }
  }
}

.accordion-header {
  align-items: flex-start;

  html[data-fontsize='AA'] & {
    align-items: flex-end;
  }

  html[data-fontsize='AAA'] & {
    align-items: flex-end;
  }

  &[data-accordion-item-element='heading'] {
    height: 100%;
    padding: em(16);

    @media #{$large-down} {
      height: 100%;
    }

    @media #{$small-down} {
      height: 100%;
    }
  }
}

.expand-button {
  min-width: em(96);
}
