@import '~assets/styles/h3Imports';

.root {
  display: grid;
  grid-template-columns: 30px auto;
}

.address-wrapper {
  align-items: center;
  display: grid;
  flex-wrap: wrap;
  gap: em(10);
  text-overflow: ellipsis;
  white-space: nowrap;
}

.address {
  color: var(--accent-interactive, #284b9b);
  font-size: em(16);
  font-weight: 600;
  line-height: em(24);
  max-width: em(200);
  white-space: normal;

  html[data-fontsize='AA'] & {
    max-width: em(280);
  }

  html[data-fontsize='AAA'] & {
    max-width: em(340);
  }

  @media #{$large-down} {
    max-width: em(300);

    html[data-fontsize='AA'] & {
      max-width: em(340);
    }

    html[data-fontsize='AAA'] & {
      max-width: em(320);
    }
  }

  @media #{$small-down} {
    max-width: 100%;

    html[data-fontsize='AA'] & {
      max-width: 100%;
    }

    html[data-fontsize='AAA'] & {
      max-width: 100%;
    }
  }
}

.distance {
  color: var(--input-rested-border-color, #606565);
  font-size: em(14);
  font-weight: 600;
  line-height: em(20);
  max-width: em(200);
  overflow: hidden;
  text-overflow: ellipsis;
}
