@import '~assets/styles/h3Imports';

.root {
  padding-bottom: em(16) !important;
  padding-left: em(16);
  padding-right: em(16);
}

.heading {
  color: var(--gray-5, #0a1414);
  font-size: em(14);
  font-weight: 600;
  line-height: em(20);
  margin-bottom: em(10);
  margin-top: em(12);

  @media #{$large-down} {
    font-size: em(16);
  }

  @media (max-width: $max-phone-size) {
    font-weight: 700;
  }
}

.provider-details {
  margin-bottom: em(8);
}

.provider-detail {
  display: inline-block;
  margin-bottom: em(5);
  margin-right: em(50);
}

.detail-title {
  color: var(--gray-3, #606565);
  font-size: em(14);
  font-weight: 600;
  line-height: em(20);
  margin-bottom: em(3);

  @media #{$small-down} {
    margin-top: em(16);
  }
}

.detail-value {
  color: var(--gray-5, #0a1414);
  font-size: em(16);
  font-weight: 600;
  line-height: em(24);
}

.services {
  display: grid;
  grid-template-columns: auto auto auto;
  list-style: none;
  padding: 0;

  @media #{$large-down} {
    grid-template-columns: auto auto;
  }

  @media #{$small-down} {
    grid-template-columns: auto;
  }
}

.service {
  display: grid;
  grid-template-columns: em(24) auto;
  margin-bottom: em(16);
  margin-top: 0;
}

.service-icon {
  svg {
    height: em(12);
    width: em(16);
  }
}
