@import '~assets/styles/h3Imports';

.root {
  display: grid;
  grid-template-columns: em(60) auto;
}

.provider-name {
  color: var(--gray-5, #0a1414);
  font-size: em(16);
  font-weight: bold;
  line-height: em(20);
  margin: 0;
  width: em(130);

  @media #{$large-down} {
    max-width: none;
  }
}

.provider-category {
  color: var(--gray-3, #606565);
  font-size: em(14);
  font-weight: 600;
  line-height: em(20);
  max-width: em(130);
  overflow: hidden;
  text-overflow: ellipsis;

  @media #{$large-down} {
    max-width: none;
  }
}

.h3-provider {
  color: var(--gray-5, #0a1414);
  font-size: em(16);
  font-weight: bold;
  line-height: em(20);
  margin: 0;
  width: em(130);
}
