@import '~assets/styles/h3Imports';

hr {
  margin: 0;
  width: 100%;
}

.root {
  display: flex;
  flex-wrap: wrap;
  gap: em(20);
  width: calc(100% - #{em(96)});

  @media #{$large-up} {
    justify-content: space-between;
    margin-right: em(20);
  }

  @media #{$large-down} {
    width: 100%;
  }

  @media #{$small-down} {
    display: grid;
  }
}

.button {
  --button-min-width: 100%;

  height: em(45);
}

.button-wrapper {
  display: flex;
  width: #{em(160)};

  @media #{$small-down} {
    width: 100%;
  }
}

.info-desktop {
  display: flex;
  flex-wrap: wrap;
  gap: em(20);
}

.info {
  align-items: flex-start;
  display: flex;
  flex-wrap: wrap;
  gap: 30px;
  justify-content: space-between;
  width: 100%;

  @media #{$small-down} {
    align-items: center;
  }
}

.label {
  color: var(--gray-5, #0a1414);
  font-size: em(16);
  font-weight: 600;
  line-height: em(20);

  @media (max-width: $max-phone-size) {
    font-weight: 700;
  }
}
